



import {Vue, Component, Watch} from 'vue-property-decorator'
import {
  State,
  Action
} from 'vuex-class'
import $ from "jquery"
@Component
export default class Animation extends Vue {
  @Action setChangeElement
  @State element
  animationData:any[]=[
    {
      time: 1000,
      delay: 0,
      name: "bounceIn",
      count: "1",
      function: 'ease'
    }
  ]

  get animation () {
    return this.animationData.map(item => `${item.name} ${item.time * 0.001}s ${item.function} ${item.delay * 0.001}s ${item.count}`)
  }

  @Watch('element')
  onElementChanged (): void {
    this.animationData = {...this.element.animation}
  }
}



